











import Vue from "vue";
import Component from "vue-class-component";
import store from "@/store";
import { PostModel } from "@/x-vue/services/interfaces";
import PostViewComponent from "@/x-vue/components/forum/PostView.vue";

@Component({
  components: {
    PostViewComponent,
  },
}) // without this, Vue lifecycle methods will not be invoked
export default class PostView extends Vue {
  get loading(): boolean {
    return store.state.postLoading;
  }
  get post(): PostModel {
    return store.state.post;
  }
  async mounted(): Promise<void> {
    store.commit("postGet");
  }
}
